/*!
 * Josh's Custom CSS Reset
 * https://www.joshwcomeau.com/css/custom-css-reset/ */
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

html {
  scroll-behavior: smooth;
}

body {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 16px;
  font-weight: bold;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  vertical-align: middle;
  width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

form {
  accent-color: #014c86;
}

button,
input,
textarea,
select {
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  text-transform: none;
  border-radius: 0;
}

input,
textarea,
select {
  background-color: #fff;
  border: solid 1px #e5e5e5;
  padding: 6px 16px;
  width: 100%;
}
input.__half,
textarea.__half,
select.__half {
  width: 50%;
}
input.__auto,
textarea.__auto,
select.__auto {
  width: auto;
}
input::placeholder,
textarea::placeholder,
select::placeholder {
  color: #b5b5b5;
}
input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
input[readonly], input:disabled,
textarea[readonly],
textarea:disabled,
select[readonly],
select:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  outline: none;
}

input[type=number] {
  -moz-appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=radio], input[type=checkbox] {
  cursor: pointer;
}

textarea {
  min-height: 80px;
  resize: vertical;
}

select {
  background: #fff url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2224%22%20height%3D%2224%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20d%3D%22M24%2C24H0V0H24Z%22%20fill%3D%22none%22%20opacity%3D%220.87%22%2F%3E%3Cpath%20d%3D%22M15.88%2C9.29%2C12%2C13.17%2C8.12%2C9.29A1%2C1%2C0%2C0%2C0%2C6.71%2C10.7l4.59%2C4.59a1%2C1%2C0%2C0%2C0%2C1.41%2C0L17.3%2C10.7a1%2C1%2C0%2C0%2C0%2C0-1.41%2C1.017%2C1.017%2C0%2C0%2C0-1.42%2C0Z%22%2F%3E%3C%2Fsvg%3E") right 8px center/24px no-repeat;
  padding-right: 32px;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
select::-ms-expand {
  display: none;
}

button,
input[type=submit],
input[type=reset] {
  background: none;
  border: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

label {
  cursor: pointer;
}

label.__radio input[type=radio], label.__checkbox input[type=checkbox] {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}

label.__radio input[type=radio].focus-visible + span::after, label.__checkbox input[type=checkbox].focus-visible + span::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: solid 2px #005fcc;
  border-radius: 3px;
}

label.__checkbox {
  display: inline-block;
  margin-right: 16px;
  margin-bottom: 16px;
  position: relative;
}
label.__checkbox input[type=checkbox] + span {
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: bold;
  background-color: #f5f5f5;
  padding: 10px;
  padding-right: 40px;
  position: relative;
  cursor: pointer;
}
label.__checkbox input[type=checkbox]:checked + span {
  background-color: rgba(1, 76, 134, 0.1);
}

label.__checkbox input[type=checkbox] + span::before {
  content: "";
  display: block;
  outline: solid 2px #333;
  width: 14px;
  height: 14px;
  margin-right: 10px;
}
label.__checkbox input[type=checkbox]:checked + span::before {
  background-color: #014c86;
  border: solid 2px #fff;
  outline: solid 2px #014c86;
}

label.__radio {
  display: inline-block;
  margin-right: 16px;
  margin-bottom: 16px;
  position: relative;
}
label.__radio input[type=radio] + span {
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: bold;
  background-color: #f5f5f5;
  padding: 10px;
  padding-right: 40px;
  position: relative;
  cursor: pointer;
}
label.__radio input[type=radio]:checked + span {
  background-color: rgba(1, 76, 134, 0.1);
}

label.__radio input[type=radio] + span::before {
  content: "";
  display: block;
  background-color: #fff;
  border-radius: 50%;
  outline: solid 2px #333;
  width: 16px;
  height: 16px;
  margin-right: 10px;
}
label.__radio input[type=radio]:checked + span::before {
  background-color: #014c86;
  border: solid 3px #fff;
  outline: solid 2px #014c86;
}

body {
  color: #333;
  font-size: 16px;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  line-height: 1.7;
}

.l-inner {
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
}

.l-header {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  align-items: center;
}
.l-header__logo {
  flex: 0 0 auto;
  width: 80px;
}

.l-index {
  overflow-x: hidden;
}

.p-header-nav {
  display: flex;
  align-items: center;
}
.p-header-nav__item + * {
  margin-left: 20px;
}

.p-index-mv {
  position: relative;
}
.p-index-mv__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  padding: 120px 80px;
  min-height: 500px;
}
.p-index-mv__title {
  font-size: 64px;
  line-height: 1.35;
}
.p-index-mv__lead {
  font-size: 20px;
  margin-top: 20px;
}
.p-index-mv__bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.p-index-mv__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

